.tooltip {
  margin-left: 8px;
  &__btn {
    @include flex-all-center;
    border-radius: 100%;
    width: 24px;
    height: 24px;
    background: rgba(20, 16, 36, 0.08);
    @include mobile {
      display: none;
     }


    svg {
      width: 4px;
      height: 14px;
    }

    &:focus {
      + .tooltip__txt {
        opacity: 1;
        visibility: visible;
      }
    }

    @include hover {
      + .tooltip__txt {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__txt {
    display: flex;
    flex-wrap: wrap;
    background: var(--light-color);
    border-radius: 5px;
    padding: 15px 20px;
    font-weight: 400;
    font-size: 16px;
    text-align: center;
    line-height: 160%;
    color: var(--dark-color);
    box-shadow: 0 2px 8px rgba(20, 16, 36, 0.1);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    
  }
}
