.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  font-weight: 400;
  font-size: 18px;
  color: var(--light-color);
  line-height: 160%;
  text-align: center;
  background-color: var(--accent-color);
  padding: 25px 30px;
  min-width: 270px;

  &--dark {
    background-color: var(--dark-color);
  }

  @include mobile {
    padding: 20px 25px;
   }

}
