.burger {
  position: relative;
  width: 44px;
  height: 44px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  display: none;

  @include high-tablet {
    display: block;
  }

  

  &__line {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%);
    border-radius: 100px;
    background-color: var(--light-color);
    width: 26px;
    height: 2px;
    transform: translateX(-50%), translateY(-50%);

    &:first-child {
      margin-top: -7px;
    }

     &:last-child {
       left: 9px;
       width: 13px;
       margin-top: 7px;
       transform: translateY(-50%);
     }

     
  }

  
}


.burger-close {
  position: absolute;
  right: var(--container-offset);
  top: 30px;
  width: 44px;
  height: 44px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  background-image: url("../img/close.svg");
  background-position: center;
  background-size: 14px 12px;
  background-repeat: no-repeat;
}